<template>
  <div>Lodash</div>
</template>

<script>
    var a = {
      age: 1,
      jobs: {
        first: 'FE'
      }
    }
    var b=a
    b.jobs.first='这个是b'
    a.jobs.first='这个是a改过'
    console.log(a);
    console.log(b);//证明这样无法实现深拷贝 b的jobs的first没变
    var c=_.cloneDeep(a)
    c.jobs.first='这个是c改过'
    console.log(c);//证明lodash实现了深拷贝
export default {
  
}
</script>

